  saveToLocalStorage() {
    try {
      localStorage.setItem('advancedVolunteers', JSON.stringify(this.volunteers));
      localStorage.setItem('advancedPrograms', JSON.stringify(this.programs));
      localStorage.setItem('advancedActivities', JSON.stringify(this.activities));
      localStorage.setItem('advancedHours', JSON.stringify(this.hours));
      localStorage.setItem('advancedCertificates', JSON.stringify(this.certificates));
      localStorage.setItem('advancedEvaluations', JSON.stringify(this.evaluations));
      localStorage.setItem('advancedAnalytics', JSON.stringify(this.analytics));
    } catch (error) {
      console.error('Error saving to localStorage:', error);
    }
  }

  loadFromLocalStorage() {
    try {
      this.volunteers = JSON.parse(localStorage.getItem('advancedVolunteers') || '[]');
      this.programs = JSON.parse(localStorage.getItem('advancedPrograms') || '[]');
      this.activities = JSON.parse(localStorage.getItem('advancedActivities') || '[]');
      this.hours = JSON.parse(localStorage.getItem('advancedHours') || '[]');
      this.certificates = JSON.parse(localStorage.getItem('advancedCertificates') || '[]');
      this.evaluations = JSON.parse(localStorage.getItem('advancedEvaluations') || '[]');
      this.analytics = JSON.parse(localStorage.getItem('advancedAnalytics') || '[]');
    } catch (error) {
      console.error('Error loading from localStorage:', error);
    }
  }

  setupEventListeners() {
    this.createVolunteer = this.createVolunteer.bind(this);
    this.createProgram = this.createProgram.bind(this);
    this.switchView = this.switchView.bind(this);
    this.handleFilterChange = this.handleFilterChange.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.viewVolunteer = this.viewVolunteer.bind(this);
    this.editVolunteer = this.editVolunteer.bind(this);
  }

